.header-secondary {
  margin: 0 -12px;
  border-bottom: 1px solid #e2e8f5;
}

.resp-vtabs {
  li {
    position: relative !important;
  }

  .resp-tab-active.active:before, li.active:before {
    content: '';
    position: absolute;
    width: 10px;
    height: 15px;
    top: 0px;
    left: 0px;
    bottom: 0;
    z-index: 1;
    display: block;
    border-style: solid;
    border-width: 0 0 25px 15px;
    border-color: #535cfd #535cfd transparent;
  }

  .resp-tab-active.active .side-menu__icon {
    fill: #535cfd;
  }

  li {
    &:hover .side-menu__icon, &.resp-tab-active .side-menu__icon {
      fill: #535cfd;
    }
  }
}